/* Body */
body {
    font-family: Arial, sans-serif;
    background-color: #f6f6f6;
  }
  
  /* Signup Container */
  .signup {
    max-width: 400px;
    margin: 50px auto;
    background: #fff;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Heading */
  .signup h1 {
    font-size: 24px;
    color: #333;
    margin-bottom: 30px;
  }
  
  /* Input Fields */
  .signup input[type="email"],
  .signup input[type="password"],
  .signup input[type="submit"] {
    width: 100%;
    padding: 12px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .signup input[type="submit"] {
    background-color: #4caf50;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .signup input[type="submit"]:hover {
    background-color: #45a049;
  }
  
  /* Error Message */
  .signup p {
    color: red;
    margin-top: -10px;
    margin-bottom: 20px;
  }
  
  
  /* Login Link */
  .signup a {
    display: block;
    text-align: center;
    text-decoration: none;
    color: #333;
  }
  
  .signup a:hover {
    text-decoration: underline;
    color: #000;
  }
  /* Or Separator */
.signup p.or {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .signup p.or::before,
  .signup p.or::after {
    content: '';
    position: absolute;
    top: 50%;
    width: 40%;
    height: 1px;
    background-color: #ccc;
  }
  
  .signup p.or::before {
    right: 60%;
  }
  
  .signup p.or::after {
    left: 60%;
  }
  