/* General Styles */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f6f6f6;
   
  }
  
  .App {
    text-align: center;
  }
  
  /* Header Styles */
  header {
    background-color: #333;
    color: white;
    padding: 2px 0;
    margin: 1px 1px 1px 1px;
  }
  
  header h1 {
    font-size: 50px;
    margin: 10px 5px 5px 5px;

  }
  
  header span#art {
    color: #ff9800;
  }
  
  
  
  /* Main Styles */
  main {
    margin-top: 5px;
  }
  
  #img-block {
    margin: 0 auto;
    width: 90vw;
    border: 2px solid #ccc;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1);
  }
  
  #image {
    width: 100%;
    height: 78vh;
    display: flex;
  }
  
  .spinner {
    border: 8px solid rgba(0, 0, 0, 0.1);
    border-left-color: #333;
    animation: spin 1s linear infinite;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin: 50px auto;
            position: fixed;
            top: 40%;
            left: 48%;
            transform: translate(-50%, -50%);
            border: 8px solid rgba(0, 0, 0, 0.1);
            border-left-color: #333;
            animation: spin 1s linear infinite;
            border-radius: 50%;
            width: 50px;
            height: 50px;
            
        
      }
      
      @keyframes spin {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
      
  
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  /* Footer Styles */
      /* Footer Styles */
footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 10px 0;
    background-color: #333;
    color: white;
    padding: 10px 0;
    background-color: #333;
    margin: 1px 1px 1px 1px;
   height: 7%;
  
  }
  
  #foot {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  #input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-right: 10px;
    width: 50%;
  }
  
  #btn {
    padding: 10px 20px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  #btn:hover {
    background-color: #8dde91;
  }
  